import React from 'react'
import { SEO } from '../../components/SEO'
import Footer from '../../components/Footer'
import Header from '../../components/Header'
import Layout from '../../components/layout/Layout'
import { StaticImage } from 'gatsby-plugin-image'

const projectOne = () => {
  return (
    <Layout>
      <Header />
      <section class='mx-auto max-w-4xl dark:text-slate-400'>
        <div class='prose md:prose-lg max-w-none container mx-auto flex flex-col items-center justify-center px-5 py-8'>
          <div class='w-full text-left lg:w-5/6'>
            <h1 class='mb-6 text-3xl font-semibold leading-tight text-gray-900 dark:text-slate-400 sm:mb-10 sm:text-4xl'>
              Handling Sales Objections | eLearning Simulation
            </h1>
          </div>
          <figure class='container mx-auto mb-6 flex w-5/6 flex-col items-center'>
            <StaticImage
              class='mb-1 rounded object-cover object-center'
              alt='Handling Sales Objections | eLearning Simulation'
              src='../../images/projects/scenario-based-learning/Lead_Project_Image.jpg'
            />
            {/* <figcaption class='text-xs sm:text-sm'>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum
              facilis perspiciatis, error nihil laborum necessitatibus tota.
            </figcaption> */}
          </figure>
          <div class='flex justify-center mb-6'>
            <button class='inline-flex rounded border-0 bg-orange-200 py-2 px-6 text-lg font-medium text-slate-700 hover:bg-orange-300 focus:outline-none shadow-md'>
              <a
                href='https://scenario-based-learning.netlify.app/'
                target={'_blank'}
              >
                Explore Project
              </a>
            </button>
          </div>
          <div class='w-full text-left lg:w-5/6'>
            {/* <h2 class='title-font mb-4 text-3xl font-medium text-gray-900 sm:text-4xl'>
              Microdosing synth tattooed vexillologist
            </h2> */}
            <p class='mb-8 leading-relaxed'>
              This scenario-based eLearning simulation helps sales executives
              deal with customers' most common hesitations about buying a
              product or signing up for a service.
            </p>
            <p class='prose mb-8 leading-relaxed dark:text-slate-400'>
              <ul className='list-disc list-outside'>
                <li>
                  <span className='font-bold'>Role:</span> Instructional Design,
                  Learning Experience Design
                </li>
                <li>
                  <span className='font-bold'>Target Audience:</span> Sales
                  Executives
                </li>
                <li>
                  <span className='font-bold'>Tools Used:</span> Articulate
                  Storyline, Create Studio Pro (for animation videos), Mind Map
                  Pro (for action mapping), Microsoft Word
                </li>
                <li>
                  <span className='font-bold'>Client:</span> RankFirst Marketing
                  (name changed to respect the client's non-disclosure
                  agreement)
                </li>
              </ul>
            </p>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Overview
            </h2>
            <p class='mb-8 leading-relaxed'>
              RankFirst Marketing was a new entrant into the development of CRM
              applications targeted towards small and medium businesses.
            </p>
            <p class='mb-8 leading-relaxed'>
              According to company's internal survey, two common hesitations
              were observed to buying the company's product. One: RankFirst was
              unheard of compared to other established players in the similar
              space. Two: most prospects had already signed up for a competitive
              service.
            </p>
            <p class='mb-8 leading-relaxed'>
              The response to these objections usually ended with offering a
              discount in order to lure the prospects. Heads in the Sales
              Department were in agreement that this tactic could lead to a
              double whammy: hurting brand perception in the long run and
              decreasing revenues in the short run.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Title Screen: Can You Handle Sales Objections?'
                src='../../images/projects/scenario-based-learning/02-Title Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Title Screen: Can You Handle Sales Objections?
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              As a result, they wanted their sales executives to recognize these
              risks and make plausible decisions. After understanding the
              nuances of this situation, I thought that the lack of knowledge
              and skill surrounding this topic was a problem worth solving.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='User Personalization for the Learner'
                src='../../images/projects/scenario-based-learning/04-User Personalization Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                User Personalization for the Learner
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              I recommended a scenario-based eLearning simulation that allows
              sales executives to make key decisions surrounding the common
              objections to buying. I also proposed a touch of gamification:
              including red flags every time a bad decision is chosen and green
              flags for more plausible decisions.
            </p>
            <p class='mb-8 leading-relaxed'>
              This would help the target learners recognize the impact of their
              choices, although in a safe eLearning environment.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt="Scenario Involving a Sales Executive's First Day at Work"
                src="../../images/projects/scenario-based-learning/05-Scenario Involving Sale's Executive First Day at Work.jpg"
              />
              <figcaption class='text-xs sm:text-sm'>
                Scenario Involving a Sales Executive's First Day at Work
              </figcaption>
            </figure>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Process
            </h2>
            <p class='mb-8 leading-relaxed'>
              After analyzing my proposal, the client saw value in the
              above-mentioned action-oriented approach and thought this would
              help successfully navigate the common objections revolving around
              closing the sale. So, we proceeded with the project.
            </p>
            <p class='mb-8 leading-relaxed'>
              I worked closely with the Head of Sales at RankFirst, who was also
              the subject matter expert (SME) for this intervention. The
              constant input from the SME helped me design and develop this
              interactive simulation in stages:
            </p>
            <p class='prose mb-8 leading-relaxed dark:text-slate-400'>
              <ul className='list-decimal list-outside'>
                <li>Making an action map</li>
                <li>Drafting a text-based storyboard in PowerPoint</li>
                <li>
                  Developing a prototype and polishing the eLearning package
                </li>
              </ul>
            </p>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Making an Action Map
            </h3>
            <p class='mb-8 leading-relaxed'>
              At the outset, the SME and I set up a kickoff meeting where I was
              exposed to two key elements. Firstly, the details of the internal
              survey, which listed common objections to closing a sale were
              shared. Next, key sales executives were made available for a
              face-to-face interview.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Action Map Showing Tangible Actions to Deal with Sales Objections'
                src='../../images/projects/scenario-based-learning/03-Action Mapping.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Action Map Showing Tangible Actions to Deal with Sales
                Objections
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              The above learning analysis formed the basis for drawing up an
              action map to identify the key actions that would improve sales
              team's handling of the common objections to closing the deal. With
              these actions laid out and the most important actions identified,
              our decision points for the scenario-based learning became clear.
            </p>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Drafting a Text-Based Storyboard on PowerPoint
            </h3>
            <p class='mb-8 leading-relaxed'>
              After identifying the most important actions to focus on, I worked
              with the SME to craft an authentic story around the actions.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Text-Based Storyboard Showing How a Scenario Would Kick Off'
                src='../../images/projects/scenario-based-learning/06-Storyboard Showing How the Scenario Kicks Off.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Text-Based Storyboard Showing How a Scenario Would Kick Off
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              After much discussion, we decided to focus the story on a new
              sales executive who would have to navigate his first day on the
              job with minimal or no guidance. He would face choices about what
              to say if a sales prospect is already working with a competing
              software vendor or how to deal with an objection connected to
              price or popularity of the company.
            </p>
            <p class='mb-8 leading-relaxed'>
              Since the learner would be driving the scenario, they would make
              the choices at key decision points and see corresponding
              consequences if they opt for a bad decision.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Storyboard Showing a Key Decision Point and its Consequences'
                src='../../images/projects/scenario-based-learning/07-Storyboard Showing a Key Decision Point and its Consequences.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Storyboard Showing a Key Decision Point and its Consequences
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              I encapsulated all of this in a text-based storyboard; it included
              the full script of the two characters, questions and decision
              choices, consequences for each choice, and results/feedback
              screen.
            </p>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Developing a Prototype and Polishing the eLearning Product
            </h3>
            <p class='mb-8 leading-relaxed'>
              Once the text-based storyboard was complete and approved, I began
              work on making the prototype to match the requirements of the
              story featuring the sales executive's first day on the job. For
              the prototype, pictures were used accompanying the voice-to-text
              narration. The prototype showcased only the key screens (using
              still images/illustrations) and functionality of red/green flags.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Sales Executive in a Conversation with a Prospect'
                src='../../images/projects/scenario-based-learning/08-Sales Executive in a Conversation with a Prospect.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Sales Executive in a Conversation with a Prospect
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              I delivered this to the client so that key stakeholders could
              review the functionality, layout and the flow.
            </p>
            <p class='mb-8 leading-relaxed'>
              The prototype was well-received by the client. Minor tweaks were
              suggested which were implemented to produce the final eLearning
              product. This featured animated videos (created in Create Studio
              Pro) for all the scenes along with the voice over narration and
              spoken dialogues for both characters.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Animation Featuring a Split Screen Conversation'
                src='../../images/projects/scenario-based-learning/09-Split Screen Conversation.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Animation Featuring a Split Screen Conversation
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              Since the text-based storyboard and prototype were already
              approved, the last phase of the project was a relatively
              straightforward task.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Key Decision Point in the Scenario Where Learner Action is Needed'
                src='../../images/projects/scenario-based-learning/10-Key Decision Point in the Scenario.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Key Decision Point in the Scenario Where Learner's Action is
                Needed
              </figcaption>
            </figure>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Screen Showing Result of a Good Decision Taken by the Learner'
                src='../../images/projects/scenario-based-learning/11-Screen Showing Result of a Good Decision Made by the Learner.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Screen Showing Result of a Good Decision Taken by the Learner
              </figcaption>
            </figure>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Screen Showing Consequence of a Bad Decision Taken by the Learner'
                src='../../images/projects/scenario-based-learning/12-Screen Showing Consequence of a Bad Decision Taken by the Learner.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Screen Showing Consequence of a Bad Decision Taken by the
                Learner
              </figcaption>
            </figure>

            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Results and Takeaways
            </h2>
            <p class='mb-8 leading-relaxed'>
              The final product was delivered to the client in time, which
              helped them integrate this package on their LMS. As decided during
              the analysis stage, the client made it mandatory for all sales
              executives to complete that course. This armed them with key
              positive performance behaviors to deal with common sales
              objections.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Course Completion Screen with Locked Exit Screen'
                src='../../images/projects/scenario-based-learning/13-Course Completion Screen with Locked Exit Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Course Completion Screen with Locked Exit Screen
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              The client appreciated the fact that this eLearning intervention
              was conceptualized keeping the key pain points of the learner in
              mind. Using the performance-oriented action mapping approach, the
              package only focused on reinforcing the actions that people need
              to perform.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Summary Screen Showing Red Flags and Corresponding Corrective Actions'
                src='../../images/projects/scenario-based-learning/14-Summary Screen Showing Red Flags and Corrective Actions.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Summary Screen Showing Red Flags and Corresponding Corrective
                Actions
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              This personalized, meaningful and active learning method led to
              better decision making on the job, and the client reported a much
              better sales conversion rate because of, at least in part, to this
              eLearning simulation.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Corrective Action is Shown when a Red Flag is Clicked'
                src='../../images/projects/scenario-based-learning/15-Corrective Actions when a Red Flag is Clicked.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Corrective Action is Shown when a Red Flag is Clicked
              </figcaption>
            </figure>
          </div>
        </div>
      </section>
      <Footer />
    </Layout>
  )
}

export default projectOne

export const Head = () => (
  <SEO title='Ankeet Patel - Scenario-Based eLearning Simulation' />
)
